
.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1030;
  background-color: #000;
  opacity: .5;
  -webkit-transition: opacity 0.4s cubic-bezier(0.35, 0, 0.25, 1);
  transition: opacity 0.4s cubic-bezier(0.35, 0, 0.25, 1);
  will-change: opacity;
}
.backdrop_out {
    opacity: 0;
}



