
.heading {
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  margin-bottom: 35px;
}
.heading__title {
    color: #363636;
    font-size: 28px;
    line-height: 1.2;
    margin: 0 0 9px;
}
@media (max-width: 767px) {
.heading__title {
        font-size: 27px;
}
}
.heading__subtitle {
    color: #909090;
    font-size: 20px;
}
@media (max-width: 767px) {
.heading__subtitle {
        font-size: 19px;
}
}



