
.faq__row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: -45px;
  margin-right: -45px;
}
@media (max-width: 991px) {
.faq__row {
      margin-left: -15px;
      margin-right: -15px;
}
}
@media (max-width: 767px) {
.faq__row {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      margin-left: 0;
      margin-right: 0;
}
}
.faq__col {
  width: 50%;
  padding: 0 45px;
}
@media (max-width: 991px) {
.faq__col {
      padding: 0 15px;
}
}
@media (max-width: 767px) {
.faq__col {
      width: 100%;
      padding: 0;
}
}
.faq__item {
  border-bottom: 1px solid rgba(137, 137, 137, 0.35);
}
.faq__link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 18px;
  font-weight: normal;
  padding: 34px 0 20px 0;
  color: #202020;
  line-height: 1.44;
}
@media (max-width: 767px) {
.faq__link {
      padding: 20px 0;
}
}
.faq__link:not(.faq__link_collapsed), .faq__link:hover {
    color: #89d46b;
}
.faq__caret {
  height: 0;
  width: 0;
  border-left: 4.5px solid transparent;
  border-right: 4.5px solid transparent;
  border-top: 7px solid currentColor;
  color: rgba(137, 137, 137, 0.35);
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  margin-left: 15px;
}
.faq__link:not(.faq__link_collapsed) .faq__caret {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.faq__desc {
  padding-bottom: 20px;
  color: #808080;
  line-height: 1.3;
  font-size: 14px;
}



