
.steps {
  background-color: #fbfbfb;
  padding: 90px 0 46px;
  position: relative;
}
.container .steps {
    margin-left: -20px;
    margin-right: -20px;
}
@media (min-width: 1047px) {
.container .steps {
        margin-left: calc((100vw - (1046px - 40px)) / -2);
        margin-right: calc((100vw - (1046px - 40px)) / -2);
}
}
.steps__shape {
    position: absolute;
    top: -10px;
    left: 0;
    width: 100%;
}
.steps__list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
@media (max-width: 767px) {
.steps__list {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
}
.steps__item {
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
}
.steps__curve {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    -ms-flex-item-align: start;
        align-self: flex-start;
    margin: 60px 20px 0;
    opacity: 0.15;
    max-width: 190px;
}
@media (max-width: 991px) {
.steps__curve {
        margin-left: 10px;
        margin-right: 10px;
}
}
@media (max-width: 767px) {
.steps__curve {
        -webkit-box-flex: 0;
            -ms-flex: none;
                flex: none;
        -ms-flex-item-align: center;
            align-self: center;
        margin: 60px 0;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        width: 100px;
}
}
.steps__curve:nth-child(4) {
      margin-top: 70px;
}
.steps__curve svg {
      max-width: 100%;
}



