
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 1040;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 30px 0;
}
.modal__dialog {
    margin: auto;
    -webkit-box-flex: 0;
        -ms-flex: 0 1 auto;
            flex: 0 1 auto;
    max-width: 100%;
    -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    -webkit-transition-property: opacity, -webkit-transform;
    transition-property: opacity, -webkit-transform;
    transition-property: opacity, transform;
    transition-property: opacity, transform, -webkit-transform;
    will-change: opacity, transform;
}
.modal__dialog_out {
      -webkit-transform: scale(0.9, 0.85);
              transform: scale(0.9, 0.85);
      opacity: 0;
}
.modal__content {
    margin: auto;
    position: relative;
    background-color: #fff;
    max-width: 100%;
    padding: 30px;
}
.modal_medium .modal__content {
    width: 421px;
    padding: 74px 39px 65px;
}
@media (max-width: 575px) {
.modal_medium .modal__content {
        padding: 51.8px 31.2px 45.5px;
}
}
.modal_large .modal__content {
    width: 564px;
    padding: 80px 68px 83px;
}
@media (max-width: 575px) {
.modal_large .modal__content {
        padding: 56px 47.6px 58.1px;
}
}
.modal__close {
    position: absolute;
    top: 0;
    right: 0;
    width: 58px;
    height: 58px;
    background-color: #f7f7f7;
    z-index: 100;
    -webkit-transition: 0.25s ease;
    transition: 0.25s ease;
    border-top-right-radius: inherit;
    cursor: pointer;
}
.modal__close:hover {
      background-color: #f3f3f3;
}
.modal__close-icon {
    color: #000;
    font-size: 22px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}



