
.step {
  width: 169px;
  text-align: center;
}
.step__circle {
    display: inline-block;
    width: 131px;
    margin-bottom: 25px;
    font-size: 0;
}
.step__icon {
    display: inline-block;
    max-width: none;
    font-size: 131px;
}
.step__desc {
    color: #292929;
    font-size: 18px;
    line-height: 20px;
}
.step__note {
    color: #a1a1a1;
    font-size: 14px;
    line-height: 16px;
    margin: 10px -6px 0;
}



