
.field {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-size: 17px;
}
.field__label {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -ms-flex-item-align: start;
        align-self: flex-start;
    color: #a1a1a1;
    -webkit-transition: 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    transition: 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    position: absolute;
    left: 1px;
    right: 1px;
    top: 0;
    bottom: 0;
    z-index: 10;
    padding: 0 23px;
    margin-top: 23px;
    line-height: 1;
    height: 1em;
    cursor: text;
    pointer-events: none;
}
.field_dirty .field__label,
  .field_focused .field__label {
    font-size: 12px;
    -webkit-transform: translateY(-13px);
            transform: translateY(-13px);
}
.field__content {
    position: relative;
}
.field__error {
    display: block;
    margin-top: 5px;
    color: #d90000;
    font-size: 12px;
    line-height: 1.5;
    padding-left: 2px;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
}
.field__check {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 15px;
    margin-top: 25px;
    font-size: 13px;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    -webkit-transition: 0.1s ease;
    transition: 0.1s ease;
    opacity: 0;
    visibility: hidden;
}
.field_dirty.field_valid .field__check {
    opacity: 1;
    visibility: visible;
}
.field_select .field__check {
    right: 40px;
}



