
.contact {
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.contact__icon {
    display: inline-block;
    vertical-align: middle;
    margin-right: 9px;
    color: #89d46b;
    position: relative;
    top: 2px;
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
}
.contact_address .contact__icon {
    font-size: 13px;
}
.contact_phone .contact__icon {
    font-size: 15px;
}
.contact__value {
    display: inline-block;
    vertical-align: middle;
    color: #292929;
    font-size: 14px;
    -webkit-box-flex: 0;
        -ms-flex: 0 1 auto;
            flex: 0 1 auto;
}



