
.checkbox {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.checkbox__control {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}
.checkbox__box {
    display: inline-block;
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
    width: 16px;
    height: 16px;
    border-radius: 3px;
    border: 1px solid #ececec;
    background-color: #ffffff;
    position: relative;
    top: 0.1em;
}
.checkbox__box:before {
      content: '';
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 2px;
      background-color: #89d46b;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      -webkit-transform: scale(0);
              transform: scale(0);
}
.checkbox__control:checked ~ .checkbox__box:before {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.checkbox__control:indeterminate ~ .checkbox__box:before {
    -webkit-transform: scale(1);
            transform: scale(1);
    height: 2px;
    border-radius: 1px;
}
.checkbox__control:focus ~ .checkbox__box {
    -webkit-box-shadow: 0 0 0 1px #fff, 0 0 0 3px rgba(137, 212, 107, 0.25);
            box-shadow: 0 0 0 1px #fff, 0 0 0 3px rgba(137, 212, 107, 0.25);
}
.checkbox__label {
    display: inline-block;
    -webkit-box-flex: 0;
        -ms-flex: 0 1 auto;
            flex: 0 1 auto;
    color: #808080;
    line-height: 1.6;
    margin-left: 6px;
}
.checkbox a {
    color: #b3b3b3;
}
.checkbox a:hover {
      color: inherit;
      text-decoration: underline;
}



