
.thanks {
  height: 340px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.thanks__content {
    text-align: center;
    margin: auto;
    max-width: 400px;
}
.thanks__heading {
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 70px;
}
.thanks__title {
    color: #000000;
    margin-bottom: 1px;
}
.thanks__subtitle {
    color: #a1a1a1;
}



