
.hamburger {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 20px;
  background: linear-gradient(0deg, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0) 8px, #333 8px, #333 12px, rgba(51, 51, 51, 0) 12px, rgba(51, 51, 51, 0) 20px);
  z-index: 1021;
  /* active states */
}
.hamburger:before, .hamburger:after {
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #333;
    content: "";
}
.hamburger:before {
    top: 0;
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}
.hamburger:after {
    bottom: 0;
    -webkit-transform-origin: top left;
            transform-origin: top left;
}
.hamburger, .hamburger:before, .hamburger:after {
    -webkit-transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    transition: 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}
.hamburger_active {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.hamburger_active:before {
    width: 75%;
    -webkit-transform: translateY(8px) rotate(-45deg);
            transform: translateY(8px) rotate(-45deg);
}
.hamburger_active:after {
    width: 75%;
    -webkit-transform: translateY(-8px) rotate(45deg);
            transform: translateY(-8px) rotate(45deg);
}



