
.input {
  width: 100%;
  padding: 29px 23px 10px;
  border-radius: 5px;
  border: 1px solid #ececec;
  background-color: #ffffff;
  font-size: 17px;
  -webkit-transition: border 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  transition: border 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}
.input::-webkit-input-placeholder {
    color: #a1a1a1;
    font-weight: 300;
}
.input:-moz-placeholder {
    opacity: 1;
    color: #a1a1a1;
    font-weight: 300;
}
.input::-moz-placeholder {
    opacity: 1;
    color: #a1a1a1;
    font-weight: 300;
}
.input:-ms-input-placeholder {
    color: #a1a1a1;
    font-weight: 300;
}
.input_focused {
    border-color: rgba(94, 174, 224, 0.45);
}
.input_error {
    border-color: #d90000;
}
.input_select {
    background: #fff url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='%23666' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E") no-repeat right 16px center;
    background-size: 10px 12.5px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}
.input_select:required:invalid {
      color: #a1a1a1;
      font-weight: 300;
}
.input_select option {
      color: #292929;
}
.input_select option:disabled[value=""] {
        display: none;
}



