
.account {
  color: #000000;
}
.account__header {
    text-align: center;
    margin-bottom: 69px;
}
.account__title {
    font-size: 18px;
    line-height: 26px;
    font-weight: 400;
}
.account__body {
    text-align: center;
    margin-bottom: 85px;
}
.account__debt-label {
    color: #909090;
    font-size: 18px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 13px;
}
.account__debt-price {
    font-size: 40px;
    font-weight: 700;
    line-height: 1;
}
.account__debt-price small {
      font-size: 65%;
}
.account__form {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0 -15.5px 31px;
}
@media (max-width: 575px) {
.account__form {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
}
}
.account__field {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    padding: 0 15.5px;
    position: relative;
}
.account__field .form-error {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      margin-left: 15.5px;
      margin-right: 15.5px;
}
@media (max-width: 575px) {
.account__field {
        margin-bottom: 50px;
}
}
.account__action {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    padding: 0 15.5px;
    position: relative;
}
.account__cards {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin-top: 10px;
    opacity: 0.7;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
}
.account__cards img {
      -webkit-box-flex: 0;
          -ms-flex: none;
              flex: none;
      margin: 0 6px 12px;
}
.account__card {
    margin: 6px;
}



