
.footer {
  background-color: #f6f6f6;
  opacity: 0.7;
  color: #a1a1a1;
  font-size: 14px;
}
.footer__content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 16px 0;
    margin: 0 -20px;
}
@media (max-width: 767px) {
.footer__content {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        text-align: center;
}
}
.footer__copyright, .footer__studio {
    margin: 10px 20px;
}
.footer__copyright a, .footer__studio a {
      color: inherit;
      text-decoration: underline;
}
.footer__copyright a:hover, .footer__studio a:hover {
        text-decoration: none;
}



