
.btn {
  cursor: pointer;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  vertical-align: middle;
  text-align: center;
  outline: none;
  border: none;
  max-width: 100%;
  -webkit-transition: 0.25s ease;
  transition: 0.25s ease;
  position: relative;
  line-height: 1.2;
  min-width: 228px;
  font-size: 20px;
  border-radius: 9px;
  padding: 21.5px 23px;
}
a.btn, a.btn:hover {
    text-decoration: none;
}
.btn[disabled] {
    opacity: .65;
    pointer-events: none;
}
.btn_small {
    min-width: 0;
    font-size: 17px;
    border-radius: 4px;
    padding: 9px 18px;
}
.btn_primary {
    -webkit-box-shadow: 0 15px 22px rgba(204, 204, 204, 0.43);
            box-shadow: 0 15px 22px rgba(204, 204, 204, 0.43);
    background-color: #89d46b;
    color: #ffffff;
    font-weight: 700;
    letter-spacing: 0.7px;
}
.btn_small.btn_primary {
    -webkit-box-shadow: 0 8px 11px rgba(204, 204, 204, 0.43);
            box-shadow: 0 8px 11px rgba(204, 204, 204, 0.43);
    font-weight: 600;
    letter-spacing: 0;
}
.btn_block {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}



